import React, { Component } from 'react';
import { Avatar, Button, Checkbox, CircularProgress, Container, Divider, FormControl, FormControlLabel, FormLabel, Grid, IconButton, InputAdornment, InputLabel, Link, Paper, Radio, RadioGroup, Select, TextField, Typography } from '@material-ui/core'
import { AccountBox, Email, Lock, School, Visibility, VisibilityOff, CheckCircle, LocationCity, Fingerprint, Error } from '@material-ui/icons'
import { initializeApp } from "firebase/app";
import { getDatabase, ref, onValue, get, off, set, push} from "firebase/database"
import iAPI from '../utils/iAPI';

const liff = window.liff;

export default class CheckIn extends Component {

    constructor(props) {
        super(props);

        this.state = {
            userLineID: "",
            pictureUrl: "",
            name: "",
            email: "",
            code: "",
            company: "",
            type: 0,
            update: "",
            workType: "",
            login_info: undefined,
            status: {
                complete: false,
                existed: false,
                message: "Getting Data...",
                icon : <CircularProgress/>
            }
        };

        this.dev = false;
    }

    componentDidMount = async () => 
    {
        if (this.dev)
        {
            this.setState({
                userLineID: "Ub365bc051529f470e72db6a27c5898f7",
                pictureUrl: "https://imagesvc.meredithcorp.io/v3/mm/image?url=https%3A%2F%2Fstatic.onecms.io%2Fwp-content%2Fuploads%2Fsites%2F47%2F2020%2F06%2F26%2Ftiny-white-kitten-873941684-2000.jpg"
            }, () => 
            {
                setTimeout(this.InitializeFirebase, 1500);
            })
            return;
        } 

        await liff.init({ liffId: "1657793014-yN02mwO4" }).catch(err => { throw err });
        if (liff.isLoggedIn()) 
        {
            let getProfile = await liff.getProfile();
            this.setState(
            {
                userLineID: getProfile.userId,
                pictureUrl: getProfile.pictureUrl,
            }, this.InitializeFirebase);
        } 
        else 
        {
            liff.login();
        }
    }


    InitializeFirebase = () => 
    {
        const firebaseConfig = 
        {
            apiKey           : "AIzaSyBsAZ-KDml8bLZP-_KPSwsxeJW6ZXlTPEw",
            authDomain       : "work-checker-innova-99364.firebaseapp.com",
            databaseURL      : "https://work-checker-innova-99364-default-rtdb.asia-southeast1.firebasedatabase.app",
            projectId        : "work-checker-innova-99364",
            storageBucket    : "work-checker-innova-99364.appspot.com",
            messagingSenderId: "890549897742",
            appId            : "1:890549897742:web:3ad79956d4046ac64e8a5f"
        };

        const app = initializeApp(firebaseConfig);
        const db = getDatabase();
        const data = ref(db, this.state.userLineID);

        let status = this.state.status;
        get(data).then((snapshot) => 
        {
            if (snapshot.val() == null)
            {
                status.complete = false;
                status.existed  = false;
                status.message  = "ไม่พบผู้ใช้";
                status.icon     = <Error fontSize='large' style={{color: 'red'}}/>
                this.setState(
                {
                    status: status
                })               
            }
            else
            {
                let value = snapshot.val();
                status.complete = true;
                status.existed  = true;

                this.setState(
                {
                    name:    value.name,
                    email:   value.email,
                    code:    value.code,
                    type:    value.type,
                    company: value.company,
                    login_info: value.login_info,
                    status: status
                })               
            }

        });
    }


    GetForms = () => 
    {
        let form = new URLSearchParams();
        if (this.dev)
        {
            alert(this.state.workType);
            form.append("entry.1940245042", this.state.email);
            form.append("entry.1642076706", this.state.company);
            form.append("entry.1176442603", this.state.name);
            form.append("entry.449446392", "🔶ออกงาน");
            form.append("entry.2090110175", this.state.type == 0 ? "🟣พนักงานประจำ" : "🟢FreeLance");
            form.append("entry.665609104", this.state.update);
        }
        else
        {
            form.append("emailAddress", this.state.email);
            form.append("entry.191266647", this.state.company);
            form.append("entry.198801696", this.state.name);
            form.append("entry.738632084", this.state.code);
            form.append("entry.197462310", "🔶ออกงาน");
            form.append("entry.1494121876", this.state.type == 0 ? "🟣พนักงานประจำ" : "🟢FreeLance");
            form.append("entry.1567980174", this.state.workType == "wfh" ? "🏡WFH" : "🏬Office");
            form.append("entry.786102667", this.state.update);
            form.append("pageHistory", "0,1,2");
            form.append("submit", "Submit");
        }
        form.append("pageHistory", "0,1,2");
        form.append("submit", "Submit");

        return form;
    }


    GetURL = () => 
    {
        if (this.dev)
        {
            return "https://docs.google.com/forms/d/e/1FAIpQLScptseT4XN75pFpIW11tWrhUZ3ACq47eKQOacXPHIj7B2-03w/formResponse";
        }
        else
        {
            //return "https://docs.google.com/forms/d/e/1FAIpQLScm-Be5BlOL2tCxheHy5kXcWKnSIXDDOUzuyWFNlGIArLWo1w/formResponse"
            return "https://docs.google.com/forms/d/e/1FAIpQLSfeaWHxxPZH72TT3rzgAeslHsC5tD8uy4aypnhxCu9g9alyEg/formResponse"
        }
    }


    OnCheckOut = () => 
    {
        if (this.state.name === "")
        {
            alert("Name is empty");
            return;
        }

        if (this.state.email === "")
        {
            alert("Email is empty");
            return;
        }

        if (this.state.code === "")
        {
            alert("Code is empty");
            return;
        }

        if (this.state.workType === "")
        {
            alert("Select Work Type");
            return;
        }

        let form = this.GetForms();
        let url  = this.GetURL();
        
        let status = this.state.status;
        status.complete = false;
        status.message = "Checking... Out";
        let self = this;
        this.setState({status: status}, () => 
        {
            iAPI.GET(url, form, (res) => 
            {
                status.message = "Syncing...";

                let db = getDatabase();
                let data = ref(db, this.state.userLineID + "/login_info");
                let log  = ref(db, "logs/" + this.state.userLineID + "/checks");

                let date_ob = new Date();
                let date    = ("0" + date_ob.getDate()).slice(-2);
                let month   = ("0" + (date_ob.getMonth() + 1)).slice(-2);
                let year    = date_ob.getFullYear();

                let hours   = ("0" + date_ob.getHours()).slice(-2);
                let minutes = ("0" + date_ob.getMinutes()).slice(-2);
                let seconds = ("0" + date_ob.getSeconds()).slice(-2);

                let login_info = {
                    checked: false,
                    date: date + "/" + month + "/" + year,
                    time: hours + ":" + minutes + ":" + seconds
                };

                let time = "";
                
                try
                {
                    if (this.state.login_info != undefined)
                    {
                        if (this.state.login_info.checked == false) 
                        {
                            time = "";
                        }
                        else
                        {
                            let date1 = this.GetDate(this.state.login_info.date, this.state.login_info.time);
                            let date2 = this.GetDate(login_info.date, login_info.time);

                            let difference = date2.getTime() - date1.getTime();
                            let h = Math.floor(difference / (1000 * 60 * 60));
                            let m = Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60));
                            h = ("0" + h).slice(-2);
                            m = ("0" + m).slice(-2);
                            time = " รวม: " + h + ":" + m + " นาที";
                        }
                    }
                }
                catch
                {
                    time = "";
                }


                self.setState({status: status}, () => 
                {
                    set(data, login_info).then(() => 
                    {
                        push(log, login_info).then(() => 
                        {
                            status.complete = false;
                            status.message  = "เวลา: " + login_info.time + time;
                            status.icon     = <CheckCircle fontSize='large' style={{color: "#00aa00"}}/>
                            self.setState(
                            {
                                status: status
                            },
                                () => setTimeout(() => liff.closeWindow(), 1500)
                            )
                        })
                    });
                })
            });
        })
    }


    GetDate = (date, time) => 
    {
        let splited = date.split("/");
        let splitedTime = time.split(":");
        for(let i = 0; i < splitedTime.length; i++)
        {
            if (splitedTime[i].length > 1) continue;
            splitedTime[i] = "0" + splitedTime[i];
        }

        let dateStr = splited[2] + "-" + splited[1] + "-" + splited[0];
        let timeStr = splitedTime[0] + ":" + splitedTime[1];
        let str = dateStr  + "T" + timeStr + "Z";
        return new Date(str);
    }


    RenderForm = () => {

        let time = "เวลางาน: -";

        try
        {
            if (this.state.login_info != undefined)
            {
                if (this.state.login_info.checked == false)
                {
                    time = "ยังไม่ได้เข้างาน";
                }
                else
                {
                    let date_ob = new Date();
                    let date    = ("0" + date_ob.getDate()).slice(-2);
                    let month   = ("0" + (date_ob.getMonth() + 1)).slice(-2);
                    let year    = date_ob.getFullYear();

                    let hours   = date_ob.getHours();
                    let minutes = date_ob.getMinutes();
                    let seconds = date_ob.getSeconds();

                    let login_info = {
                        checked: false,
                        date: date + "/" + month + "/" + year,
                        time: hours + ":" + minutes + ":" + seconds
                    };
                    
                    let date1 = this.GetDate(this.state.login_info.date, this.state.login_info.time);
                    let date2 = this.GetDate(login_info.date, login_info.time);

                    let difference = date2.getTime() - date1.getTime();
                    let h = Math.floor(difference / (1000 * 60 * 60));
                    let m = Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60));
                    h = ("0" + h).slice(-2);
                    m = ("0" + m).slice(-2);
                    
                    time = "เวลางาน: " + h + ":" + m + " น.";
                }
            }
        }
        catch {}



        return (
            <Grid container spacing={2} direction="column" justifyContent="center" alignItems="center">
                {/* Logo */}
                <Grid item container spacing={1} direction="row" justifyContent='center' alignItems='center'>
                    <Grid item xs={4}>
                        <Avatar src={this.state.pictureUrl} alt="logo" style={{width:64, height: 64}}/>
                    </Grid>
                    <Grid item container xs={8} direction="column" justifyContent="center">
                        <Grid item>
                            <Typography variant='h6'>{this.state.name}</Typography>
                        </Grid>
                        <Grid item>
                            <Typography variant='subtitle2'>{this.state.code}</Typography>
                        </Grid>
                    </Grid>
                </Grid>

                {/* Username */}
                <Grid item container spacing={1} direction="row">
                    <Grid item xs={12}>
                        <TextField
                            label="Update Progress"
                            type="textarea"
                            multiline={true}
                            minRows={3}
                            placeholder="What did you do?"
                            value={this.state.update}
                            onChange={(e) => this.setState({ update: e.target.value })}
                            variant="outlined"
                        />
                    </Grid>
                </Grid>

                <Grid item xs={12}>
                    <FormControl>
                        <FormLabel id="demo-row-radio-buttons-group-label">Work Type</FormLabel>
                        <RadioGroup
                            row
                            aria-labelledby="demo-row-radio-buttons-group-label"
                            name="row-radio-buttons-group"
                            value={this.state.workType}
                            onChange={(e) => this.setState({ workType: e.target.value})}
                        >
                            <FormControlLabel value="office" control={<Radio />} label="Office" />
                            <FormControlLabel value="wfh" control={<Radio />} label="WFH" />
                        </RadioGroup>
                    </FormControl>
                </Grid>

                <Grid item>
                    <Typography variant='subtitle1'>{time}</Typography>
                </Grid>


                {/* Button */}
                <Grid item container spacing={1} direction="row">
                    <Grid item xs={12} style={{ width: "100%" }}>
                        <Button variant="contained" style={{backgroundColor: "#be380c", color: "#ffffff"}} onClick={this.OnCheckOut.bind(this)} fullWidth={true}>
                            { (this.dev ? "Check Out Dev" : "Check Out")}
                        </Button>
                    </Grid>
                </Grid>
            </Grid>
        )
    }


    render() 
    {
        let centerV = {
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
        }

        return (
            <div style={{width: "100vw", height: "100vh", backgroundPosition: "fixed", backgroundRepeat: true, backgroundImage: "url(https://ar360kstc.s3.ap-southeast-1.amazonaws.com/background_out.jpg)"}}>
                <Container style={centerV}> 
                    <Grid container direction="row" justifyContent="center" alignItems="center">
                        <Paper style={{padding: 24, backgroundColor: "#ffffffee"}} elevation={3}>
                            <Grid item>
                                {
                                    this.state.status.complete && (
                                        this.RenderForm()
                                    )
                                }
                                {
                                    !this.state.status.complete && (
                                        <Grid container spacing={2} direction='row' justifyContent='center' alignItems='center'>
                                            <Grid item>
                                                {this.state.status.icon}
                                            </Grid>
                                            <Grid item>
                                                <Typography variant='subtitle1'>{this.state.status.message}</Typography>
                                            </Grid>
                                        </Grid>
                                    )
                                }
                            </Grid>
                        </Paper>
                    </Grid>
                </Container>
            </div>
        )

    }
}